import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableContainer,
  TableHead,
  TableRow,
  Stack,
} from "@mui/material";
import * as S from "./CompareTable.styled";
import { useAppSelector } from "src/store/hooks";
import { compareNumbers, compareTemperature } from "./CompareTable.helpers";
import { tableHeaders } from "./CompareTable.const";

export const CompareTable = () => {
  const data = useAppSelector((store) => store.weather.citiesData);
  const searchedCity = useAppSelector((store) => store.weather.searchedData);

  if (searchedCity === null || data.length === 0) {
    return null;
  }

  const { feelslike, humidity, pressure, windspeed, cloudcover } =
    searchedCity.currentConditions;

  return (
    <Box p={1}>
      <Card variant="outlined">
        <CardContent>
          <Stack rowGap={2}>
            <Typography fontWeight="bold" fontSize="1.2rem">
              Comparison
            </Typography>
            <TableContainer component={Card} variant="outlined">
              <Table>
                <TableHead>
                  <TableRow>
                    {tableHeaders.map((cell, index) => (
                      <S.StyledTableCell key={cell}>{cell}</S.StyledTableCell>
                    ))}
                  </TableRow>
                </TableHead>
                <TableBody>
                  {data.map((city) => (
                    <S.StyledTableRow key={city.resolvedAddress}>
                      <S.StyledTableCell> {city.address} </S.StyledTableCell>
                      <S.StyledTableCell>
                        {compareTemperature(
                          feelslike,
                          city.currentConditions.feelslike
                        )}
                      </S.StyledTableCell>
                      <S.StyledTableCell>
                        {compareNumbers(
                          humidity,
                          city.currentConditions.humidity,
                          "%"
                        )}
                      </S.StyledTableCell>
                      <S.StyledTableCell>
                        {compareNumbers(
                          windspeed,
                          city.currentConditions.windspeed,
                          "km"
                        )}
                      </S.StyledTableCell>
                      <S.StyledTableCell>
                        {compareNumbers(
                          cloudcover,
                          city.currentConditions.cloudcover,
                          "%"
                        )}
                      </S.StyledTableCell>
                      <S.StyledTableCell>
                        {compareNumbers(
                          pressure,
                          city.currentConditions.pressure,
                          "mbar"
                        )}
                      </S.StyledTableCell>
                    </S.StyledTableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Stack>
        </CardContent>
      </Card>
    </Box>
  );
};
